.home__hero-section {
  color: #fff;
  padding-top: 80px;
  padding-bottom: 140px; 
  
}

.home__hero-row {
  align-items: center;
}

.row {
  display: flex;
  margin-right: -15px;
  margin-bottom: -15px;
  margin-left: -15px;
  flex-wrap: wrap;
  align-content: stretch;
}

.col {
  margin-bottom: 15px;
  padding-right: 15px;
  padding-left: 15px;
  flex: 1;
  max-width: 50%;
  flex-basis: 50%;
}

.home__hero-text-wrapper {
  max-width: 540px;
  padding-top: 0;
  padding-bottom: 60px;
}

.top-line {
  color: #fff;
  font-size: 26px;
  line-height: 30px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  margin-bottom: 16px;
}

.heading {
  margin-bottom: 24px;
  font-size: 48px;
  line-height: 1.1;
  font-weight: 600;
  color:  rgb(43, 147, 243);
}

.dark {
  color: #1c2237;
}

.darkBg {
  background-color: #1c2237;
}

.home__hero-subtitle {
  max-width: 440px;
  margin-bottom: 35px;
  font-size: 20px;
  line-height: 28px;
}

.home__hero-img-wrapper {
  max-width: 555px;
}

.home__hero-img {
  max-width: 95%;
  margin-top: 0;
  margin-right: 0;
  margin-left: 10px;
  padding-right: 0;
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
}



.content{
  display: flex;
  justify-content: center;
  margin-top: 185px;
}



@media screen and (max-width: 800px) {
  .container {
    padding-right: 30px;
    padding-left: 30px;

  }
}

@media screen and (max-width: 800px) {


  .home__hero-text-wrapper {
   
    padding-top: 0;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 60px;
    align-content: center;
    /* font-size: 8px; */
  }

  .home__hero-img-wrapper {
    max-width: 555px;
    padding-left: 40px ;
    padding-right: 40px;
    /* font-size: 8px; */
  }
  
  
  .col {
    max-width: 100%;
    flex-basis: 100%;

  }
  }

  @media screen and (max-width: 500px) {


  .top-line {
    font-size: 30px;
    font-weight: 500;


  }

  .heading{
        font-size: 35px;
        font-weight: 700;

  }


  .home__hero-text-wrapper {
   
    padding-top: 0;
    padding-left: 20px;
    padding-right: 30px;
    padding-bottom: 50px;
    align-content: center;
    /* font-size: 8px; */
  }


  .home__hero-img-wrapper {
    max-width: 755px;
    padding-left: 0px ;
    padding-right: 0px;
    /* font-size: 8px; */
  }


}

.light{
  /* font-size: 20px; */
  padding-top: 20px;
  padding-right: 50px;
  padding-left:50px ;
  line-height: 35px;
  font-weight: 100;
}