:root{
    --primary:  rgb(43, 147, 243);
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
    white-space: nowrap;
}

.hyphen-btn{
    color:  rgb(43, 147, 243);
    font-size: x-large;
    background-color: transparent;
    border-radius: 15px;
    border: 1px solid white;
    padding: 15px 60px;
}

.hyphen-btn:hover{
    background-color: #ffffff;
    transition: all 0.5s ease-out;
}

.btn--primary{
    background-color: var(--primary);
    color: #000000; 
    border: 1px solid var(--primary);
}

.btn--outline{
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn-medium {
    padding: 8px 20px;
    font-size: 18px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--mobile {
    text-align: center;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 24px;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
}

.btn--wide{
    padding: 12px 64px;
    font-size: 20px;
}

.btn--large:hover, .btn--medium:hover, .btn--mobile:hover{
    transition: all 0.3s ease-out;
    background: #fff;
    color: #111111;
}

.btn--wide:hover{
    color:#fff;
    background-color: #000;
    transition: all 0.2s ease-out;
}

.blue {
    background-color: #ffc400;
    color: #000;
    border:none;
}

.red {
    background-color: #f00946;
    color: #fff;
    border:none;
}

.primary {
    background-color:  #111111;
    color: #fff;
    border:none;
}
.primary:hover{
    background-color: #fff;
    color: #111111;
    border:none;
}

.green {
    border-color: #25ce4a;
    color: #fff;
    border:none;
}

.green:hover{
    background-color: #111111;
}

.btn-link{
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 8px 16px;
    height: 100%;
    width: 100%;
}